* {
  font-family: "Roboto Flex", sans-serif;
}

/* Display Large */
.display-lg {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 64px;
  font-size: 57px;
  font-weight: 400;
  letter-spacing: 0;
  /* margin-top: 12px; */
}

/* Display Medium */
.display-md {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 52px;
  font-size: 45px;
  font-weight: 400;
  letter-spacing: 0;
  /* margin-top: 12px; */
}

/* Display Small */
.display-sm {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 44px;
  font-size: 36px;
  font-weight: 400;
  letter-spacing: 0;
  /* margin-top: 10px; */
}

/* Header Large */
.header-lg {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 40px;
  font-size: 32px;
  font-weight: 400;
  letter-spacing: 0;
}

/* Header Medium */
.header-md {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 36px;
  font-size: 28px;
  font-weight: 400;
  letter-spacing: 0;
}

/* Header Small */
.header-sm {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 32px;
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 0;
}

/* Title Large */
.title-lg {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 28px;
  font-size: 22px;
  font-weight: 400;
  letter-spacing: 0;
}

/* Title Medium */
.title-md {
  font-family: "Roboto Flex Medium", sans-serif;
  line-height: 24px;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0.15px;
}

/* Title Small */
.title-sm {
  font-family: "Roboto Flex Medium", sans-serif;
  line-height: 20px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.1px;
}

/* Label Large */
.label-lg {
  font-family: "Roboto Flex Medium", sans-serif;
  line-height: 20px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.1px;
}

/* Label Medium */
.label-md {
  font-family: "Roboto Flex Medium", sans-serif;
  line-height: 16px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.5px;
}

/* Label Small */
.label-sm {
  font-family: "Roboto Flex Medium", sans-serif;
  line-height: 16px;
  font-size: 11px;
  font-weight: 500;
  letter-spacing: 0.5px;
}

/* Body Large */
.body-lg {
  font-family: "Roboto Flex Medium", sans-serif;
  line-height: 24px;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.5px;
}

/* Body Medium */
.body-md {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 20px;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.25px;
}

/* Body Small */
.body-sm {
  font-family: "Roboto Flex Regular", sans-serif;
  line-height: 16px;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.4px;
}

